import { Link } from 'react-router-dom';
import { Bars3Icon, XMarkIcon } from '@heroicons/react/24/outline';
import { useNavigation } from '../../hooks/useNavigation';
import DesktopMenu from './DesktopMenu';
import MobileMenu from './MobileMenu';

const Navigation = () => {
  const {
    isOpen,
    desktopOpenDropdown,
    mobileOpenDropdown,
    location,
    toggleMenu,
    closeMenu,
    toggleMobileDropdown,
    handleDesktopDropdownChange,
  } = useNavigation();

  return (
    <nav className="fixed w-full bg-cloud-white/90 backdrop-blur-md z-50 shadow-sm">
      <div className="container mx-auto px-4">
        <div className="flex justify-between items-center h-16">
          <Link to="/" className="text-2xl font-bold font-serif text-jeju-ocean hover:text-ocean-mist transition-colors">
            
          </Link>

          {/* Desktop Menu */}
          <DesktopMenu
            location={location}
            desktopOpenDropdown={desktopOpenDropdown}
            onOpenChange={handleDesktopDropdownChange}
          />

          {/* Mobile Menu Button */}
          <div className="md:hidden">
            <button
              onClick={toggleMenu}
              className="text-coastal-gray hover:text-jeju-ocean transition-colors"
              aria-label={isOpen ? " " : " "}
              aria-expanded={isOpen}
            >
              {isOpen ? (
                <XMarkIcon className="h-6 w-6" />
              ) : (
                <Bars3Icon className="h-6 w-6" />
              )}
            </button>
          </div>
        </div>
      </div>

      {/* Mobile Menu */}
      <MobileMenu
        isOpen={isOpen}
        location={location}
        mobileOpenDropdown={mobileOpenDropdown}
        onClose={closeMenu}
        onToggleDropdown={toggleMobileDropdown}
      />
    </nav>
  );
};

export default Navigation;
